.am-checkbox {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 21 * 0.01rem;
  height: 21 * 0.01rem;
}

.am-checkbox-inner {
  position: absolute;
  right: 0;
  width: 21 * 0.01rem;
  height: 21 * 0.01rem;
  border: 1 * 0.01rem solid #ccc;
  border-radius: 50%;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  box-sizing: border-box;
}

.am-checkbox-inner:after {
  position: absolute;
  display: none;
  top: 1.5 * 0.01rem;
  right: 6 * 0.01rem;
  z-index: 999;
  width: 5 * 0.01rem;
  height: 11 * 0.01rem;
  border-style: solid;
  border-width: 0 1 * 0.01rem 1 * 0.01rem 0;
  content: '\0020';
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.am-checkbox-input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  border: 0 none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.am-checkbox.am-checkbox-checked .am-checkbox-inner {
  border-color: #108ee9;
  background: #108ee9;
}

.am-checkbox.am-checkbox-checked .am-checkbox-inner:after {
  display: block;
  border-color: #fff;
}

.am-checkbox.am-checkbox-disabled {
  opacity: 0.3;
}

.am-checkbox.am-checkbox-disabled.am-checkbox-checked .am-checkbox-inner {
  border-color: #888;
  background: none;
}

.am-checkbox.am-checkbox-disabled.am-checkbox-checked .am-checkbox-inner:after {
  border-color: #888;
}

.am-list .am-list-item.am-checkbox-item .am-list-thumb {
  width: 21 * 0.01rem;
  height: 21 * 0.01rem;
}

.am-list .am-list-item.am-checkbox-item .am-list-thumb .am-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 44 * 0.01rem;
}

.am-list .am-list-item.am-checkbox-item .am-list-thumb .am-checkbox-inner {
  left: 15 * 0.01rem;
  top: 12 * 0.01rem;
}

.am-list .am-list-item.am-checkbox-item.am-checkbox-item-disabled .am-list-content {
  color: #bbb;
}

.am-checkbox-agree {
  position: relative;
  display: flex;
  align-items: stretch;
  margin-left: 15 * 0.01rem;
  padding-top: 9 * 0.01rem;
  padding-bottom: 9 * 0.01rem;
}

.am-checkbox-agree .am-checkbox {
  position: absolute;
  left: 0;
  top: 0;
  width: 2 * 15 * 0.01rem;
  height: 100%;
}

.am-checkbox-agree .am-checkbox-inner {
  left: 0;
  top: 3 * 0.01rem + 9 * 0.01rem;
}

.am-checkbox-agree .am-checkbox-agree-label {
  display: inline-block;
  font-size: 15 * 0.01rem;
  color: #000;
  line-height: 1.5;
  margin-left: 2 * 15 * 0.01rem;
  margin-top: 1PX;
}